@import '../../../../includes/assets/css/consts.scss';
@import '../base/mixins';

.dpit-theme-sticky {

	.dpit-arrow {
		display: none !important;
	}

	.dpit-tooltip-number {

		color: rgba(var(--accent-contrast-color), 0.85);

		&--on-tooltip {
			position: fixed;
			color: rgb(var(--accent-contrast-color));
			left: unit(20);
			border-color: rgba(var(--accent-color), 0.85) !important;
		}


		&:not(.dpit-tooltip-number--on-tooltip) {
			z-index: -1 !important;
		}
	}

	.dpit-tooltip {
		position: fixed;
		min-width: unset;
		max-width: 100vw;
		bottom: 0;
		left: 0 !important;
		top: unset !important;
		right: 0 !important;
		margin-left: 0;
		border-top: 4px solid rgb(var(--accent-color));
		padding: unit(50) unit(50);
		display: flex;
		align-items: stretch;
		border-radius: 0;

		&--with-progress-bar {
			border-top-width: 0;
		}

		&--on-top {
			bottom: initial !important;
			top: 0 !important;
			border-top-width: 0;
			border-bottom: 4px solid rgb(var(--accent-color));

			&.dpit-tooltip--with-progress-bar {
				border-bottom-width: 0;
			}
		}




		@include atXS {
			padding: unit(30) unit(60);
		}

		@include atXXS {
			flex-direction: column;
		}

		&--with-number {
			padding-left: unit(120);
		}
	}

	.dpit-tooltip-text {
		color: rgba(var(--text-color), 1);
		padding: unit(10) unit(20);
		min-height: unit(110);
		flex: 1;
		max-height: 35vh;
		overflow-y: auto;


		@include atXS {
			min-height: unset;
		}
	}

	.dpit-tooltip-buttons {
		flex: 0;
		align-items: flex-end;
		padding-top: unit(100);

		@include atXXS {
			padding-top: unit(30);
		}
	}

	.dpit-button {
		box-sizing: border-box;
		background-color: rgba(var(--accent-color), 0.3);
		color: rgba(var(--accent-contrast-inv-color), 0.9);
		font-weight: 600;
		text-transform: uppercase;
		border: 1px solid rgba(var(--accent-contrast-inv-color), 0.3);

		&:not(.dpit-disabled) {

			&:focus {
				color: rgb(var(--accent-contrast-color));
				background-color: rgb(var(--accent-color), 0.86);
			}

			&:active,
			&:hover,
			&.hovered {
				color: rgb(var(--accent-contrast-color));
				background-color: rgb(var(--accent-color));
			}
		}

		&--prev::not(.dpit-button--full) {
			border-right-width: 0;
		}

		&--skip:not(.dpit-button--done) {
			box-sizing: border-box;
			background-color: rgba(var(--accent-color), 0.15);
			margin-right: unset;
			position: absolute;
			top: unit(40);
			right: unit(50);
			//border-radius: 50%;
			padding: unset;
			width: unit(70);
			height: unit(70);
			border: 1px solid rgba(var(--accent-contrast-inv-color), 0.15);


			@include transitionMulti($transition-time-faster, ease-in, background-color);

			&::before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;

				background-image: url('./../../icons/dp-close-cross-dark.svg');
				background-position: center;
				background-size: 60%;
				background-repeat: no-repeat;
			}


			&:focus {
				background-color: rgba(var(--accent-color), 0.25);
			}

			&:active,
			&:hover,
			&.hovered {
				background-color: rgba(var(--accent-color), 0.75);

				&::before {
					background-image: url('./../../icons/dp-close-cross-light.svg');
				}
			}

		}
	}


	.dpit-bullets {
		position: fixed;
		top: unit(30);
		left: 50%;
		transform: translate(-50%, 0);
		padding-top: 0.3vw;
		@include transitionFast(opacity);

		ul {
			max-width: 70vw;

			@media (hover: hover) {

				&::-webkit-scrollbar-track {
					background-color: rgba(var(--accent-contrast-color), 0.2);
					outline: 1px solid rgba(var(--accent-contrast-inv-color), 0.7);
				}

				&::-webkit-scrollbar-thumb {
					background-color: rgba(var(--accent-contrast-color), 0.4);
				}
			}

			li {
				margin: 0.4vw 0.35vw;
				width: 2vw;
				height: 2vw;
				border-radius: var(--button-border-radius);
				background: linear-gradient(315deg, rgba(var(--accent-color), 0.4) 25%, rgba(var(--accent-color), 0.4) 100%), linear-gradient(315deg, rgb(var(--accent-contrast-color)) 25%, rgb(var(--accent-contrast-color)) 100%);
				outline: 1px solid rgba(var(--accent-contrast-inv-color), 0.7);
				display: flex;
				justify-content: center;
				align-items: center;
				@include transitionFast(transform, box-shadow);

				>div {
					line-height: 1;
					font-weight: 600;
					flex-shrink: 1;
					font-size: unit(20);
					color: rgba(var(--accent-contrast-inv-color), 0.95);
				}

				&.active,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--accent-color), 0.05) 25%, rgba(var(--accent-color), 0.1) 100%), linear-gradient(315deg, rgb(var(--accent-contrast-color)) 25%, rgb(var(--accent-contrast-color)) 100%);

					>div {
						display: block;
					}
				}

				&:active,
				&.active {
					&:hover {
						background: rgb(var(--accent-contrast-color));
					}

					transform: scale(1.3);
					background-color: rgb(var(--accent-color), 0.95);
					margin-left: 0.5vw;
					margin-right: 0.5vw;

					cursor: pointer;

					>div {
						display: block;
						font-size: unit(25);
					}
				}



				@include atXS {
					width: unit(40);
					height: unit(40);
					margin: unit(10) unit(6);

					&:active,
					&.active {
						margin-left: unit(10);
						margin-right: unit(10);
					}
				}
			}

			@include atXS {
				max-width: 90vw;
				width: 90vw;
				//flex-wrap: wrap;
			}
		}
	}


	.dpit-tooltip--on-top {
		.dpit-bullets {
			top: unset;
			bottom: unit(30);
		}
	}


	.dpit-progress {
		position: absolute;
		width: 100%;
		left: 0;
		top: 0;
		height: unit(12);
		margin: 0;
		background-color: rgba(var(--accent-color), 0.5);
	}

	.dpit-tooltip--with-progress-bar.dpit-tooltip--on-top {
		.dpit-progress {
			top: unset;
			bottom: 0;
		}
	}

	.dpit-progress-bar {
		background-color: rgba(var(--accent-color), 0.767);
	}

	&.dpit-light-accent {
		.dpit-button {
			&--skip {
				&::before {
					background-image: url('./../../icons/dp-close-cross-light.svg');
				}

				&:active,
				&:hover,
				&.hovered {

					&::before {
						background-image: url('./../../icons/dp-close-cross-dark.svg');
					}
				}

			}
		}

		.dpit-bullets {
			ul {
				li {
					background: linear-gradient(315deg, rgba(var(--accent-contrast-color), 0.7) 25%, rgba(var(--accent-contrast-color), 0.85) 100%), linear-gradient(315deg, rgb(var(--accent-color)) 25%, rgb(var(--accent-color)) 100%);
					outline: 1px solid rgba(var(--accent-contrast-inv-color), 0.3);

					&.active,
					&:hover {
						background: linear-gradient(315deg, rgba(var(--accent-contrast-color), 0.1) 25%, rgba(var(--accent-contrast-color), 0.3) 100%), linear-gradient(315deg, rgba(var(--accent-color), 1) 25%, rgba(var(--accent-color), 1) 100%);
					}

					&.active {
						&:hover {
							background: rgb(var(--accent-color));
						}

						cursor: pointer;
					}
				}
			}
		}
	}
}